import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Row, Col, message } from 'antd';
import classNames from 'classnames';
import CopyToClipboard from 'react-copy-to-clipboard';

import CustomButton from '../../components/UI/CustomButton/CustomButton';
import { NFT_TYPE, ZPOINTSTATISTIC_POINT_KEYS } from '../../constants/misc';
import CustomModal from '../../components/UI/CustomModal/CustomModal';
import NumberInput from '../../components/UI/NumberInput/NumberInput';
import {
  useEarnedZPoint,
  usePhase1Config,
  useStakeZPointRewardPerDay,
  useZPointStatistic,
} from '../../hooks/useManager';
import { formatNumber } from '../../utils/utils';
import { useInviterRecords } from '../../hooks/useInvitationCenter';
import { useNFTRecords } from '../../hooks/useNFT';

import styles from './Profile.module.scss';
import commonStyles from '../../common.module.scss';

const REFERRAL_DESC = ['Desc 1', 'Desc 2', 'Desc 3', 'Desc 4'];

const ACTION_TYPE = {
  STAKE: 'Stake',
  UNSTAKE: 'Unstake',
};

const NftDetail = ({ nftRecord, phase1ZbcLockAmount }) => {
  const imgPath = `/static/images/NFT/${
    nftRecord.nftType === NFT_TYPE.ZEPOCH ? 'zepoch' : 'themis'
  }.png`;
  return (
    <div className={styles.NftDetail}>
      <div className={styles.tagWrap}>
        <div className={classNames(commonStyles.gradientTitle, styles.phaseTag)}>
          Phase {nftRecord.phase}
        </div>
        <div className={styles.stakeTag}>Staked</div>
      </div>
      <img src={imgPath} alt="node" />
      <div className={styles.contentWrap}>
        <h2 className={classNames(styles.nftId, commonStyles.gradientTitle)}>{`#${formatNumber(
          nftRecord.tokenId,
          '000',
        )}`}</h2>
        <h1 className={classNames(styles.title, commonStyles.gradientTitle)}>
          {nftRecord.nftType}
        </h1>
        {nftRecord.phase === 1 && (
          <div className={styles.infoItemGroup}>
            <div className={styles.infoItem}>
              <h2>Status</h2>
              <p>{nftRecord.phase1ZbcUserLocked.unlocked ? 'Unlocked' : 'Locked'}</p>
            </div>
            <div className={styles.infoItem}>
              <h2>ZBC Amount</h2>
              <p>{formatNumber(phase1ZbcLockAmount)}</p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

NftDetail.propTypes = {
  // eslint-disable-next-line
  nftRecord: PropTypes.objectOf(PropTypes.any).isRequired,
  phase1ZbcLockAmount: PropTypes.number,
};

NftDetail.defaultProps = {
  phase1ZbcLockAmount: 0,
};

export default function Profile() {
  const [nftAmount, setNftAmount] = useState(1);
  const [action, setAction] = useState(ACTION_TYPE.STAKE);
  const [selectedTab, setSelectedTab] = useState(NFT_TYPE.ZEPOCH);
  const [stakeModalVisible, setStakeModalVisible] = useState(false);
  const [nftDetailVisible, setNftDetailVisible] = useState(false);
  const [selectedNft, setSelectedNft] = useState({});
  const [referralDetailVisible, setReferralDetailVisible] = useState(false);

  const { data: zPointStatistic } = useZPointStatistic();
  const { data: earnedZPoint } = useEarnedZPoint();
  const { data: stakeZPointRewardPerDay } = useStakeZPointRewardPerDay();
  // console.log(stakeZPointRewardPerDay);
  const { inviterRecords, inviteeRecordsLength } = useInviterRecords();
  const referralCode = inviterRecords?.data?.invitationCode;
  const referralLink = `${window.location.origin}/#/participate?referral=${referralCode}`;
  const { data: nftRecords } = useNFTRecords();
  const { zepochNodeRecords, themisGavelRecords } = nftRecords;
  const idleZepochNode = zepochNodeRecords?.filter((item) => !item.staked);
  const stakedZepochNode = zepochNodeRecords?.filter((item) => item.staked);
  const { data: phase1Config } = usePhase1Config();
  const { phase1ZbcLockAmount } = phase1Config;

  const toggleStakeModalVisible = () => {
    setStakeModalVisible((prev) => !prev);
  };

  const toggleNftDetailVisible = () => {
    setNftDetailVisible((prev) => !prev);
  };

  const toggleReferralDetailVisible = () => {
    setReferralDetailVisible((prev) => !prev);
  };

  return (
    <div className={styles.Profile} style={{ backgroundImage: 'url(/static/images/About/bg.jpg)' }}>
      <div className={styles.banner}>
        <Row gutter={[8, 8]} align="bottom" className={styles.bannerContainer}>
          <Col span={24} sm={12} lg={6}>
            <div className={styles.bannerInfo}>
              <h2>Nodes Amount</h2>
              <p>{formatNumber(zepochNodeRecords?.length)}</p>
            </div>
          </Col>
          <Col span={24} sm={12} lg={6}>
            <div className={styles.bannerInfo}>
              <h2>Total Earned Points</h2>
              <p>
                {formatNumber(
                  Object.keys(zPointStatistic).length &&
                    ZPOINTSTATISTIC_POINT_KEYS.reduce(
                      (accum, key) => accum + zPointStatistic[key],
                      0,
                    ),
                )}
              </p>
            </div>
          </Col>
          <Col span={24} sm={12} lg={6}>
            <div className={styles.bannerInfo}>
              <h2>Referrals</h2>
              <p>{formatNumber(inviteeRecordsLength?.data)}</p>
            </div>
          </Col>
          <Col span={24} sm={12} lg={6}>
            <div className={styles.bannerInfo}>
              <h2>Referral Earned</h2>
              <p>{formatNumber(zPointStatistic.commissionUsdt)} USDT</p>
            </div>
          </Col>
        </Row>
      </div>
      <div className={styles.contentContainer}>
        {/* <Row className={styles.mobileJustifyCenter}>
          <h2 className={styles.secondaryTitle}>Nodes</h2>
        </Row>
        <Row className={styles.mobileJustifyCenter}>
          <p className={styles.subtitle}>{zepochNodeRecords?.length} Nodes Found</p>
        </Row> */}
        <Row className={styles.tabsContainer} gutter={[16, 16]}>
          <Col span={24}>
            <Row>
              {Object.values(NFT_TYPE).map((item) => (
                <Col
                  span={12}
                  // lg={12}
                  className={classNames(styles.tabSelector, {
                    [styles.tabSelected]: item === selectedTab,
                  })}
                  onClick={() => {
                    setSelectedTab(item);
                  }}
                  key={item}>
                  {item}
                </Col>
              ))}
            </Row>
          </Col>
          <Row className={styles.nftList} gutter={[16, 16]}>
            {selectedTab === NFT_TYPE.ZEPOCH &&
              zepochNodeRecords?.map((nftRecord) => (
                <Col
                  span={24}
                  sm={12}
                  md={6}
                  xl={4}
                  onClick={() => {
                    setSelectedNft(nftRecord);
                    toggleNftDetailVisible();
                  }}
                  key={nftRecord.tokenId}>
                  <div className={styles.nodeContainer}>
                    <div className={styles.tagWrap}>
                      <div className={classNames(commonStyles.gradientTitle, styles.phaseTag)}>
                        Phase {nftRecord.phase}
                      </div>
                      <div className={styles.stakeTag}>Staked</div>
                    </div>

                    <img src="/static/images/NFT/zepoch.png" alt="Node" />
                    <h3>{`${NFT_TYPE.ZEPOCH} #${formatNumber(nftRecord.tokenId, '000')}`}</h3>
                  </div>
                </Col>
              ))}
            {selectedTab === NFT_TYPE.THEMIS &&
              themisGavelRecords?.map((nftRecord) => (
                <Col span={24} sm={12} md={6} xl={4} key={nftRecord.tokenId}>
                  <div className={styles.nodeContainer}>
                    <img src="/static/images/NFT/themis.png" alt="Node" />
                    <h3>{`${NFT_TYPE.THEMIS} #${formatNumber(nftRecord.tokenId, '000')}`}</h3>
                  </div>
                </Col>
              ))}
          </Row>

          {selectedTab === NFT_TYPE.ZEPOCH && (
            <Col span={24}>
              <Row className={styles.nodeInfosListContainer}>
                <Col span={24} sm={12} lg={6}>
                  <div className={styles.nodeTotalInfoContainer}>
                    <h3>Total Points by Staking</h3>
                    <p>{formatNumber(zPointStatistic.zPointStaking)}</p>
                  </div>
                </Col>
                <Col span={24} sm={12} lg={6}>
                  <div className={styles.nodeInfoContainer}>
                    <h3>Points Claimable</h3>
                    <p>{formatNumber(earnedZPoint)}</p>
                    <CustomButton className={styles.nodeInfoContainerButton} rounded>
                      Claim
                    </CustomButton>
                    {/* <p>24:00:00</p> */}
                    {/* countdown here */}
                  </div>
                </Col>
                <Col span={24} sm={12} lg={6}>
                  <div className={styles.nodeInfoContainer}>
                    <h3>Idle Nodes</h3>
                    <p>{formatNumber(idleZepochNode?.length)}</p>
                    <CustomButton
                      className={styles.nodeInfoContainerButton}
                      rounded
                      onClick={() => {
                        setAction(ACTION_TYPE.STAKE);
                        setNftAmount(1);
                        toggleStakeModalVisible();
                      }}>
                      Stake Modal
                    </CustomButton>
                    <p>
                      Stake to earn {formatNumber(stakeZPointRewardPerDay)} points per node everyday
                    </p>
                  </div>
                </Col>
                <Col span={24} sm={12} lg={6}>
                  <div className={styles.nodeInfoContainer}>
                    <h3>Staked Nodes</h3>
                    <p>{formatNumber(stakedZepochNode?.length)}</p>
                    <CustomButton
                      className={styles.nodeInfoContainerButton}
                      rounded
                      onClick={() => {
                        setAction(ACTION_TYPE.UNSTAKE);
                        setNftAmount(1);
                        toggleStakeModalVisible();
                      }}>
                      Unstake Modal
                    </CustomButton>
                    <p>
                      Stake to earn {formatNumber(stakeZPointRewardPerDay)} points per node everyday
                    </p>
                  </div>
                </Col>
              </Row>
            </Col>
          )}
        </Row>

        <div className={styles.referralContainer}>
          <Row className={styles.mobileJustifyCenter}>
            <h2 className={styles.secondaryTitle}>Referrals</h2>
          </Row>
          {referralCode && (
            <>
              <h3>Invite your friends by the link below</h3>
              <p>Your Invitation Link</p>
              <Row gutter={32} className={styles.referralLinkWrap}>
                <Col span={24} md={18}>
                  <input placeholder={referralLink} disabled />
                </Col>
                <Col>
                  <CopyToClipboard
                    text={referralLink}
                    onCopy={() => {
                      message.success('Referral link copied');
                    }}>
                    <CustomButton secondary rounded>
                      Copy
                    </CustomButton>
                  </CopyToClipboard>
                </Col>
              </Row>
            </>
          )}
          {!referralCode && (
            <h3>
              Please participate in the Zebec Operation Horizon to activate the referral system
            </h3>
          )}
          <button type="button" onClick={toggleReferralDetailVisible}>
            {referralDetailVisible ? ' Detail' : 'Detail '}
          </button>
          <ul
            className={classNames(styles.referralDetail, { [styles.show]: referralDetailVisible })}>
            {REFERRAL_DESC.map((item) => (
              <li key={item}>{item}</li>
            ))}
          </ul>
        </div>
      </div>

      <CustomModal open={stakeModalVisible} onCancel={toggleStakeModalVisible}>
        <Row className={styles.stakeContainer} justify="center" align="middle">
          <Col span={24}>
            <h3>{action} amount</h3>
            <NumberInput value={nftAmount} onChange={(v) => setNftAmount(v)} />
            <p>
              <span>Available:&nbsp;</span>
              <span>10</span>
            </p>
            <CustomButton
              rounded
              onClick={() => {
                toggleStakeModalVisible();
              }}>
              {action}
            </CustomButton>
          </Col>
        </Row>
      </CustomModal>

      <CustomModal open={nftDetailVisible} onCancel={toggleNftDetailVisible} width={300}>
        <NftDetail nftRecord={selectedNft} phase1ZbcLockAmount={phase1ZbcLockAmount} />
      </CustomModal>
    </div>
  );
}
